import React from 'react'
import Menu from '@material-ui/core/Menu'
import MenuItem from '@material-ui/core/MenuItem'
import MenuIcon from '@material-ui/icons/Menu'
import { IconButton, Typography } from '@material-ui/core'

class SimpleMenu extends React.Component {
  state = {
    anchorEl: null
  }

  handleClick = event => {
    this.setState({ anchorEl: event.currentTarget })
  }

  handleClose = () => {
    this.setState({ anchorEl: null })
  }

  render() {
    const { anchorEl } = this.state

    return (
      <div>
        <IconButton
          aria-owns={anchorEl ? 'dropdown-navbar' : undefined}
          aria-haspopup="true"
          onClick={this.handleClick}>
          <MenuIcon style={{ color: 'white', transform: 'scale(1.2)' }} />
        </IconButton>
        <Menu
          id="dropdown-navbar"
          anchorEl={anchorEl}
          open={Boolean(anchorEl)}
          onClose={this.handleClose}>
          {/*  <h1>
                        <i className='fa fa-paw' style={{ fontSize: '30px' }}></i>
                        Vem com a gente!
                    </h1> */}

          <MenuItem onClick={this.handleClose}>
            <Typography variant="h5" color="inherit" style={{ fontWeight: 'bold' }}>
              <i className="fa fa-facebook" style={{ verticalAlign: 'bottom', fontSize: '20px' }} />
              Curta no Facebook
            </Typography>
          </MenuItem>
          <MenuItem onClick={this.handleClose}>
            <Typography variant="h5" color="inherit" style={{ fontWeight: 'bold' }}>
              <i className="fa fa-play" style={{ verticalAlign: 'bottom', fontSize: '20px' }} />
              Baixe na Google Play
            </Typography>
          </MenuItem>
          <MenuItem onClick={this.handleClose}>
            <Typography variant="h5" color="inherit" style={{ fontWeight: 'bold' }}>
              <i className="fa fa-apple" style={{ verticalAlign: 'bottom', fontSize: '20px' }} />
              Baixe na App Store
            </Typography>
          </MenuItem>
          <MenuItem onClick={this.handleClose} style={{ position: 'fixed', border: 'none' }}>
            <Typography variant="h5" color="inherit" style={{ fontWeight: 'bold' }}>
              <i
                className="fa fa-angle-left"
                style={{ verticalAlign: 'bottom', fontSize: '20px' }}
              />
              Voltar
            </Typography>
          </MenuItem>
        </Menu>
      </div>
    )
  }
}

export default SimpleMenu
